import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { isNullOrUndefined } from '../shared/functions/util';
import { Login } from '../models/login';
import { HttpClient } from '@angular/common/http';
import { AuthToken } from '../models/authToken';
import { Observable } from 'rxjs';
import { RefreshToken } from '../models/refresh-token';
import { JwtHelperService } from '@auth0/angular-jwt';


@Injectable({
    providedIn: 'root'
})
export class AuthService {

    private jwtHelper: JwtHelperService = new JwtHelperService();

    constructor(
        private http: HttpClient,
    ) { }

    urlBase = `${environment.urlBaseApi}Auth/`;

    numberRefresh: any;

    public login(login: Login): Observable<AuthToken> {
        localStorage.removeItem('currentUser');
        return this.http.post<AuthToken>(this.urlBase + 'Login', login);
    }

    public needRefreshToken(): void {
        var currentUser = localStorage.getItem('currentUser');
        if (currentUser) {
            const token: AuthToken = JSON.parse(currentUser);

            var dadosToken = this.jwtHelper.decodeToken(token.token);

            if (!isNullOrUndefined(token) && !this.jwtHelper.isTokenExpired(token.token)) {

                this.refreshToken(token);
            }

            // if (isNullOrUndefined(this.numberRefresh)) {

            //     this.numberRefresh = setInterval(() => {

            //         this.needRefreshToken();

            //     }, 1000 * 60);

            //     console.log(`RefreshToken: ${this.numberRefresh} - iniciado`);
            // } else {
            //     if (!isNullOrUndefined(token)) {

            //         // const acquired = new Date(token.adquirido || '');
            //         // const valid = acquired.setSeconds(acquired.getSeconds() + (token.expiraEm || 0) - (60 * 15));
            //         // const result = new Date(valid - Date.now()).getTime() / 1000;
            //         // console.log(`RefreshToken: ${this.numberRefresh} - expira em: ${(Math.floor(result / 60))}:${Math.floor(result % 60)}`);
            //     }
            // }
        }
    }

    public refreshToken(refresh: RefreshToken): void {

        this.http.post<AuthToken>(this.urlBase + 'RefreshToken', refresh)
            .subscribe({
                next: (res) => {
                    localStorage.setItem('currentUser', JSON.stringify(res));
                },
                error: (err) => {
                    console.log(err);
                    this.logout();
                }
            });
    }

    logout() {
        localStorage.removeItem('currentUser');
    }
}
