
export function isArray(object: any): boolean {
    return Array.isArray(object);
}

/**
 * 
 * @param object 
 * @returns 
 */
export function isNullOrUndefined(object: any): boolean {
    return object === null || object === undefined;
}
/**
 * 
 * @param object 
 * @returns 
 */
export function isFunction(object: any): boolean {
    return object === 'function';
}

/**
 * Compara dois objetos de json
 * @param a Objeto 1
 * @param b Objeto 2
 */
export function jsonEqual(a: any, b: any) {
    return JSON.stringify(a) === JSON.stringify(b);
}

/**
 * Valida se a string pode ser convertida em numero.
 * @param value String a ser testada, ex: "234"
 */
export function isNumber(value: string | number): boolean {
    return ((value != null) && !isNaN(Number(value.toString())));
}

export function isValidDate(value: string) {
    const date = value.match(/^(19[5-9][0-9]|20[0-4][0-9]|2050)[-/](0?[1-9]|1[0-2])[-/](0?[1-9]|[12][0-9]|3[01])$/);
    return date !== null;
}
export function isDateString(date: string) {
    return !isNaN(Date.parse(date))
}

export function addDays(date: Date, days: number) {
    date.setDate(date.getDate() + days);
    return date;
}
export function substract(date: Date, days: number) {
    date.setDate(date.getDate() - days);
    return date;
}
/**
 * Cria o HASH da string
 */
export function hashCode(str: string) {
    var hash = 0;
    if (str.length == 0) return hash;
    for (var i = 0; i < str.length; i++) {
        var char = str.charCodeAt(i);
        hash = ((hash << 5) - hash) + char;
        hash = hash & hash; // Convert to 32bit integer
    }
    return hash;
}

