/**
 * Converte uma model em uma lista de parametros
 * @param data Model que ser convertida
 */
export function params(data: { [x: string]: string; }) {
    const result = Object.keys(data).map(key => {
        let value: string;
        if (isNullOrUndefined(data[key])) {
            value = `${key.trim()}=NULL`;
        } else if (isArray(data[key])) {
            const array = Array.from(data[key]);
            value = `(${key.trim()} in(${array.map(ax => {
                if (isNumber(ax)) {
                    value = `${ax.trim()}`;
                } else {
                    value = `"${ax.trim()}"`;
                }
                return value;
            }).join(',')}))`;
        } else if (data[key]!.toString().toLocaleLowerCase().includes('and')) {
            value = data[key].split('and').map(v => {
                if (v.includes('>') || v.includes('<')) {
                    if (isNumber(v)) {
                        value = `${key.trim()}${v.trim()}`;
                    } else {
                        const idx = (v.indexOf('=')) + 1;
                        const sinal = v.substring(0, idx).trim();
                        const str = v.substring(idx).trim();
                        return `${key.trim()}${sinal}"${str}"`;
                    }
                }
                return `${key.trim()}${v.trim()}`;
            }).join(' and ');
        } else {
            const v = data[key].toString();
            if (v.includes('>') || v.includes('<')) {
                if (isNumber(v)) {
                    value = `${key.trim()}${v.trim()}`;
                } else {
                    const idx = (v.indexOf('=')) + 1;
                    const sinal = v.substring(0, idx).trim();
                    const str = v.substring(idx).trim();
                    value = `${key.trim()}${sinal}"${str}"`;
                }
            } else {
                if (isNumber(v)) {
                    value = `${key.trim()}=${v.trim()}`;
                } else {
                    value = `${key.trim()}="${v.trim()}"`;
                }
            }
        }
        return `${value}`;
    }).join(' and ');

    return encodeURIComponent(result);
}

/**
 * Remove as propriedades da model que no tenha valores
 * @param obj Model que ser processada.
 */
export function removeEmpty(obj: any) {
    Object.keys(obj).forEach((key) => (isNullOrUndefined(obj[key]) || !obj[key] || (isArray(obj[key]) && Array.from(obj[key]).length === 0)) && delete obj[key]);
    return obj;
}