import { DOCUMENT } from '@angular/common';
import { Inject, Injectable, Renderer2, RendererFactory2 } from '@angular/core';
import { isFunction } from '../../functions/util';

@Injectable({
  providedIn: 'root'
})
export class NotifyService {

  private defaults = {
    element: 'body',
    position: null,
    type: "info",
    allow_dismiss: true,
    allow_duplicates: true,
    newest_on_top: false,
    showProgressbar: false,
    placement: {
      from: "bottom",
      align: "right"
    },
    offset: 20,
    spacing: 10,
    z_index: 1031,
    delay: 3000,
    timer: 1000,
    url_target: '_blank',
    mouse_over: null,
    animate: {
      enter: 'animated fadeInDown',
      exit: 'animated fadeOutUp'
    },
    onShow: null,
    onShown: null,
    onClose: null,
    onClosed: null,
    icon_type: 'class',
  };
  private animations: any = {};
  private notifications: any[] = [];
  private renderer: Renderer2;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    rendererFactory: RendererFactory2,
  ) {
    this.renderer = rendererFactory.createRenderer(null, null);
  }

  public info(content: INotifyOptions, setting?: INotifySettings) {
    var opt = {};
    Object.assign(opt, { type: 'info' }, setting);
    this.Notify(content, opt);
  }

  public error(content: INotifyOptions, setting?: INotifySettings) {
    var opt = {};
    Object.assign(opt, { type: 'danger' }, setting);
    this.Notify(content, opt);
  }

  public warning(content: INotifyOptions, setting?: INotifySettings) {
    var opt = {};
    Object.assign(opt, { type: 'warning' }, setting);
    this.Notify(content, opt);
  }

  public sucess(content: INotifyOptions, setting?: INotifySettings) {
    var opt = {};
    Object.assign(opt, { type: 'success' }, setting);
    this.Notify(content, opt);
  }

  private Notify(content: INotifyOptions, setting: INotifySettings) {

    var contentObj = {
      content: {
        message: typeof content === 'object' ? content.message : content,
        title: content.title ? content.title : '',
        icon: content.icon ? content.icon : '',
        url: content.url ? content.url : '#',
        target: content.target ? content.target : '-'
      }
    };
    var settings: any = {};

    Object.assign(setting, contentObj);
    Object.assign(settings, this.defaults, setting);

    if (settings.content.target === "-") {
      settings.content.target = settings.url_target;
    }
    this.animations = {
      start: 'webkitAnimationStart oanimationstart MSAnimationStart animationstart',
      end: 'webkitAnimationEnd oanimationend MSAnimationEnd animationend'
    };

    if (typeof settings.offset === 'number') {
      settings.offset = {
        x: settings.offset,
        y: settings.offset
      };
    }

    if (settings.allow_duplicates || (!settings.allow_duplicates && !this.isDuplicateNotification(this))) {
      this.init(settings);
    }
  }
  private init(settings: any) {

    var ele = this.buildNotify(settings);
    if (settings.content.icon) {
      this.setIcon(settings, ele);
    }
    if (settings.content.url != "#") {
      this.styleURL(settings, ele);
    }
    this.styleDismiss(settings, ele);
    this.placement(settings, ele);
    this.bind(settings, ele);

  }

  private buildNotify(settings: any): any {
    var content = settings.content;
    var $ele = this.renderer.createElement('div') as HTMLElement;
    //add class
    this.renderer.addClass($ele, `col-11`);
    this.renderer.addClass($ele, `col-md-4`);
    this.renderer.addClass($ele, `alert`);
    this.renderer.addClass($ele, `alert-${settings.type}`);

    //add attrib
    this.renderer.setAttribute($ele, 'data-notify', 'container');
    this.renderer.setAttribute($ele, 'data-notify-position', settings.placement.from + '-' + settings.placement.align);
    this.renderer.setAttribute($ele, 'role', 'alert');

    var btClose = this.renderer.createElement('button');
    this.renderer.addClass(btClose, `close`);
    this.renderer.setAttribute(btClose, 'type', 'button');
    this.renderer.setAttribute(btClose, 'aria-hidden', 'true');
    this.renderer.setAttribute(btClose, 'data-notify', 'dismiss');
    this.renderer.appendChild($ele, btClose);

    var iBtClose = this.renderer.createElement('i');
    var iValue = this.renderer.createText('close');
    this.renderer.addClass(iBtClose, `material-icons`);
    this.renderer.appendChild(iBtClose, iValue);
    this.renderer.appendChild(btClose, iBtClose);

    var i = this.renderer.createElement('i');
    this.renderer.addClass(i, `material-icons`);
    this.renderer.setAttribute(i, 'data-notify', 'icon');
    this.renderer.appendChild($ele, i);

    var spanTitle = this.renderer.createElement('span');
    var title = this.renderer.createText(content.title);
    this.renderer.setAttribute(spanTitle, 'data-notify', 'title');
    this.renderer.appendChild(spanTitle, title);
    this.renderer.appendChild($ele, spanTitle);

    var spanMessage = this.renderer.createElement('span');
    var message = this.renderer.createText(content.message);
    this.renderer.setAttribute(spanMessage, 'data-notify', 'message');
    this.renderer.appendChild(spanMessage, message);
    this.renderer.appendChild($ele, spanMessage);

    var divProgress = this.renderer.createElement('div');
    this.renderer.addClass(divProgress, `progress`);
    this.renderer.setAttribute(divProgress, 'data-notify', 'progressbar');
    this.renderer.appendChild($ele, divProgress);

    var divProgressBar = this.renderer.createElement('div');
    this.renderer.addClass(divProgressBar, `progress-bar`);
    this.renderer.addClass(divProgressBar, `progress-bar-${settings.type}`);
    this.renderer.setAttribute(divProgressBar, 'role', 'progressbar');
    this.renderer.setAttribute(divProgressBar, 'aria-valuenow', '0');
    this.renderer.setAttribute(divProgressBar, 'aria-valuemin', '0');
    this.renderer.setAttribute(divProgressBar, 'aria-valuemax', '100');
    this.renderer.setStyle(divProgressBar, 'width', '0%');
    this.renderer.appendChild(divProgress, divProgressBar);

    var a = this.renderer.createElement('a');
    this.renderer.setAttribute(a, 'href', `${content.url}`);
    this.renderer.setAttribute(a, 'target', `${content.target}`);
    this.renderer.setAttribute(a, 'data-notify', 'url');
    this.renderer.appendChild($ele, a);

    if (!settings.allow_dismiss) {
      var el = $ele.querySelector('[data-notify="dismiss"]');
      this.renderer.setStyle(el, 'display', 'none');
    }
    if ((settings.delay <= 0 && !settings.showProgressbar) || !settings.showProgressbar) {
      $ele.querySelector('[data-notify="progressbar"]')?.remove();
    }

    this.notifications.push($ele);

    return $ele;
  }

  private setIcon(settings: any, $ele: any) {

    this.renderer.addClass($ele, `alert-with-icon`);
    var iconEl = $ele.querySelector('[data-notify="icon"]');

    if (settings.icon_type.toLowerCase() === 'class') {
      var icon = this.renderer.createText(settings.content.icon);
      this.renderer.appendChild(iconEl, icon);
    } else {
      if ($ele.find('[data-notify="icon"]').is('img')) {
        //     this.$ele.find('[data-notify="icon"]').attr('src', settings.content.icon);
      } else {
        //     this.$ele.find('[data-notify="icon"]').append('<img src="' + settings.content.icon + '" alt="Notify Icon" />');
      }
    }
  }

  private styleDismiss(settings: any, $ele: any) {
    var el = $ele.querySelector('[data-notify="dismiss"]');
    this.renderer.setStyle(el, 'position', 'absolute');
    this.renderer.setStyle(el, 'right', '5px');
    this.renderer.setStyle(el, 'top', '15px');
    this.renderer.setStyle(el, 'marginTop', '-9px');
    this.renderer.setStyle(el, 'zIndex', settings.z_index + 2);
  }

  private styleURL(settings: any, $ele: any) {
    var el = $ele.querySelector('[data-notify="url"]');
    this.renderer.setStyle(el, 'backgroundImage', 'url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7)');
    this.renderer.setStyle(el, 'height', '100%');
    this.renderer.setStyle(el, 'left', '0');
    this.renderer.setStyle(el, 'position', 'absolute');
    this.renderer.setStyle(el, 'top', '0');
    this.renderer.setStyle(el, 'width', '100%');
    this.renderer.setStyle(el, 'zIndex', settings.z_index + 1);
  }

  private placement(settings: any, $ele: any) {
    var offsetAmt = settings.offset.y;
    var css: any = {
      display: 'inline-block',
      margin: '15px auto',
      position: settings.position ? settings.position : (settings.element === 'body' ? 'fixed' : 'absolute'),
      transition: 'all .5s ease-in-out',
      zIndex: settings.z_index
    };
    var hasAnimation = false;

    var els = this.document.querySelectorAll('[data-notify-position="' + settings.placement.from + '-' + settings.placement.align + '"]:not([data-closing="true"])');
    els.forEach((el: any) => {
      offsetAmt = Math.max(offsetAmt, parseInt(el.style[settings.placement.from]) + el.offsetHeight + parseInt(settings.spacing));
    });

    if (settings.newest_on_top === true) {
      offsetAmt = settings.offset.y;
    }
    css[settings.placement.from] = offsetAmt + 'px';

    switch (settings.placement.align) {
      case "left":
      case "right":
        css[settings.placement.align] = settings.offset.x + 'px';
        break;
      case "center":
        css.left = 0;
        css.right = 0;
        break;
    }

    Object.keys(css).map((x: any) => {
      this.renderer.setStyle($ele, x, css[x]);
    });

    settings.animate.enter.split(' ').map((x: any) => {
      this.renderer.addClass($ele, x,);
    });

    Array('webkit-', 'moz-', 'o-', 'ms-', '').forEach((prefix) => {
      this.renderer.setStyle($ele, prefix + 'AnimationIterationCount', 1);
    });

    this.animations.start.split(' ').map((x: any) => {
      this.renderer.listen($ele, x, () => {
        hasAnimation = true;
      });
    });

    this.animations.end.split(' ').map((x: any) => {
      this.renderer.listen($ele, x, () => {
        if (isFunction(settings.onShown)) {
          settings.onShown.call(this);
        }
      });
    });

    var body = this.document.querySelector(settings.element);
    this.renderer.appendChild(body, $ele);

    if (settings.newest_on_top === true) {
      offsetAmt = (parseInt(offsetAmt) + parseInt(settings.spacing)) + $ele.offsetHeight;
      this.reposition(settings, $ele, offsetAmt);
    }

    if (isFunction(settings.onShow)) {
      settings.onShow.call($ele);
    }

    setTimeout(() => {
      if (!hasAnimation) {
        if (isFunction(settings.onShown)) {
          settings.onShown.call(this);
        }
      }
    }, 600);
  }

  private bind(settings: any, $ele: any) {

    var close = $ele.querySelector('[data-notify="dismiss"]');
    this.renderer.listen(close, 'click', () => this.close(settings, $ele));

    this.renderer.listen($ele, 'mouseover', () => {
      this.renderer.setAttribute($ele, 'data-hover', 'true');
    });

    this.renderer.listen($ele, 'mouseout', () => {
      this.renderer.setAttribute($ele, 'data-hover', 'false');
    });

    this.renderer.setAttribute($ele, 'data-hover', 'false');


    if (settings.delay > 0) {
      this.renderer.setAttribute($ele, 'notify-delay', settings.delay);

      var timer = setInterval(() => {
        var delay = parseInt($ele.getAttribute('notify-delay')) - settings.timer;
        if (($ele.getAttribute('data-hover') === 'false' && settings.mouse_over === "pause") || settings.mouse_over != "pause") {
          var percent = ((settings.delay - delay) / settings.delay) * 100;
          this.renderer.setAttribute($ele, 'notify-delay', delay.toString());
          if (settings.showProgressbar) {
            var progressbar = $ele.querySelector('[data-notify="progressbar"] > div');
            this.renderer.setAttribute(progressbar, 'aria-valuenow', percent.toString());
            this.renderer.setStyle(progressbar, 'width', percent + '%')
          }
        }
        if (delay <= -(settings.timer)) {
          clearInterval(timer);
          this.close(settings, $ele);
        }
      }, settings.timer);
    }
  }

  private close(settings: any, $ele: any) {
    var posX = parseInt($ele.style[settings.placement.from]);
    var hasAnimation = false;

    this.renderer.setAttribute($ele, 'data-closing', 'true');

    settings.animate.exit.split(' ').map((x: any) => {
      this.renderer.addClass($ele, x,);
    });

    this.reposition(settings, $ele, posX);

    if (isFunction(settings.onClose)) {
      settings.onClose.call($ele);
    }

    this.animations.start.split(' ').map((x: any) => {
      this.renderer.listen($ele, x, () => {
        hasAnimation = true;
      });
    });

    this.animations.end.split(' ').map((x: any) => {
      this.renderer.listen($ele, x, () => {
        $ele.remove();
        if (isFunction(settings.onClosed)) {
          settings.onClosed.call(this);
        }
      });
    });

    setTimeout(() => {
      if (!hasAnimation) {
        $ele.remove();
        if (settings.onClosed) {
          settings.onClosed($ele);
        }
      }
    }, 600);
  }

  private reposition(settings: any, $ele: any, posX: any) {

    var index = this.notifications.indexOf($ele);
    var $elements = this.notifications.slice(index + 1, this.notifications.length);
    if (settings.newest_on_top === true) {
      $elements = this.notifications.slice(0, index);
    }
    $elements.forEach((el) => {
      this.renderer.setStyle(el, settings.placement.from, posX + 'px');
      posX = (parseInt(posX) + parseInt(settings.spacing)) + el.offsetHeight;
    });
    this.notifications.splice(index, 1);
  }

  private isDuplicateNotification(notification: any) {
    var isDupe = false;
    var notifies = this.document.querySelectorAll('[data-notify="container"]');

    notifies.forEach((el) => {

      var title = el.querySelector('[data-notify="title"]')?.outerHTML.trim();
      var message = el.querySelector('[data-notify="message"]')?.outerHTML.trim();

      var isSameTitle = title === notification.settings.content.title.trim();
      var isSameMsg = message === notification.settings.content.message.trim();
      var isSameType = el.classList.contains('alert-' + notification.settings.type);

      if (isSameTitle && isSameMsg && isSameType) {
        isDupe = true;
      }
      return !isDupe;
    });

    return isDupe;
  }
}
