import { Component, OnInit, ViewChild, ElementRef, Inject, Renderer2 } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { Router } from '@angular/router';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  private sidebar_mini_active: boolean = false;
  private mobile_menu_visible: boolean = false;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  navbarToggler() {
    var html = this.document.querySelector('html');
    var body = this.document.querySelector('body');
    var toggle = this.document.getElementById('btNavbarToggler');

    if (this.mobile_menu_visible == true) {
      this.renderer.removeClass(html, 'nav-open');
      var close = this.document.querySelector('.close-layer');

      setTimeout(() => {
        this.renderer.removeClass(toggle, 'toggled');
      }, 400);

      this.mobile_menu_visible = true;
    } else {

      setTimeout(() => {
        this.renderer.addClass(toggle, 'toggled');
      }, 400);

      var layer = this.renderer.createElement('div');
      this.renderer.addClass(layer, 'close-layer');

      this.renderer.listen(layer, 'click', () => {
        this.renderer.removeClass(html, 'nav-open');
        this.mobile_menu_visible = false;

        setTimeout(() => {
          this.renderer.removeChild(div, layer);
          this.renderer.removeClass(toggle, 'toggled');
        }, 400);
      });

      var div = body?.querySelector('.main-panel') || body?.querySelector('off-canvas-sidebar');

      this.renderer.appendChild(div, layer);

      setTimeout(() => {
        this.renderer.addClass(layer, 'visible');
      }, 100);

      this.renderer.addClass(html, 'nav-open');
      this.mobile_menu_visible = false;
    }
  }

  minimizeSidebar() {
    var body = this.document.querySelector('body');

    if (this.sidebar_mini_active == true) {
      this.renderer.removeClass(body, 'sidebar-mini');
      this.sidebar_mini_active = false;
    } else {
      this.renderer.addClass(body, 'sidebar-mini');
      this.sidebar_mini_active = true;
    }

    // simula a alterao de tamanho
    var simulateWindowResize = setInterval(function () {
      window.dispatchEvent(new Event('resize'));
    }, 180);

    // limpa a simulao depois de terminar
    setTimeout(function () {
      clearInterval(simulateWindowResize);
    }, 1000);
  }

  onLogout(e: any) {
    e.preventDefault();
    localStorage.removeItem('isLoggedin');

    if (!localStorage.getItem('isLoggedin')) {
      this.router.navigate(['/auth/login']);
    }
  }




}
