import { Component, OnInit, AfterViewInit, Renderer2, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { MENU } from './menu';
import { MenuItem } from './menu.model';
import { Router, NavigationEnd } from '@angular/router';
import { isNullOrUndefined } from 'src/app/shared/functions/util';
import { animate, state, style, transition, trigger } from '@angular/animations';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss'],
  animations: [
    trigger('indicatorRotate', [
      state('up', style({ transform: 'rotate(0deg)' })),
      state('down', style({ transform: 'rotate(180deg)' })),
      transition('down <=> up',
        animate('225ms cubic-bezier(0.4,0.0,0.2,1)')
      ),
    ]),
    trigger('slide', [
      state('up', style({ height: 0 })),
      state('down', style({ height: '*' })),
      transition('up <=> down', animate(200))
    ])
  ]
})
export class SidebarComponent implements OnInit, AfterViewInit {

  menuItems: MenuItem[] = [];
  public dataColor: string;
  public databackgroundColor: string;
  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    public router: Router) {

    this.menuItems = MENU;
    this.dataColor = localStorage.getItem('data-color') || 'green';
    this.databackgroundColor = localStorage.getItem('data-background-color') || 'white';

    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.menuItems.forEach(item => {
          this.resetMenu(item);
        });

        this.menuItems.forEach(item => {
          if (this.activeMenu(item, event.url))
            return;
        });
      }
    });
  }

  activeMenu(item: MenuItem, link: string): boolean {
    if (link.includes('editar'))
      link = link.substring(0, link.indexOf('editar'));

    if (item.link === link) {
      item.active = true;
      return true;
    } else if (this.hasItems(item)) {
      item.subItems?.forEach(subItem => {
        if (this.activeMenu(subItem, link)) {
          item.active = true;
          item.expanded = true;
          return true;
        }
        return false;
      });
    }
    return false;
  }

  resetMenu(item: MenuItem) {
    item.active = false;
    item.expanded = false;
    if (this.hasItems(item))
      item.subItems?.forEach(sub => {
        this.resetMenu(sub);
      });
  }

  ngOnInit(): void {
    if (!isNullOrUndefined(this.dataColor) && !isNullOrUndefined(this.dataColor)) {
      var sidebar = this.document.querySelector('.sidebar');
      this.renderer.setAttribute(sidebar, 'data-color', this.dataColor)
      this.renderer.setAttribute(sidebar, 'data-background-color', this.databackgroundColor)
    }
  }

  ngAfterViewInit(): void {

  }

  hasItems(item: MenuItem) {
    return item.subItems !== undefined ? item.subItems.length > 0 : false;
  }

  activateMenuItems(item: MenuItem) {
    return item.active ? 'active' : '';
  }

  getState(item: MenuItem) {

    if (item.expanded) {
      return 'down';
    } else {
      return 'up';
    }
  }

  getCaps(item: MenuItem) {
    if (item.icon)
      return null;
    return item.label?.charAt(0).toUpperCase();
  }

  toggle(item: MenuItem) {
    if (this.hasItems(item)) {
      this.menuItems.forEach(element => {
        if (element === item) {
          if (item.expanded === undefined)
            item.expanded = false;
          item.expanded = !item.expanded;
        } else {
          element.expanded = false;
        }
      });
    }
  }
}