import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './layout/layout.component';
import { ConceitoComponent } from './pages/conceito/conceito.component';
import { MateriaisComponent } from './pages/materiais/materiais.component';
import { SecretariaComponent } from './pages/secretaria/secretaria.component';
import { VideosComponent } from './pages/videos/videos.component';

const routes: Routes = [
  { path: '*', pathMatch: 'full', redirectTo: 'conceito' },
  { path: '', pathMatch: 'full', redirectTo: 'conceito' },
  { path: 'lock', loadChildren: () => import('./pages/lock/lock.module').then(x => x.LockModule) },
  { path: '404', loadChildren: () => import('./pages/not-found/not-found.module').then(x => x.NotFoundModule) },
  {
    path: '',
    component: LayoutComponent,
    children: [
      { path: 'conceito', pathMatch: 'full', component: ConceitoComponent },
      { path: 'materiais', component: MateriaisComponent },
      { path: 'videos', component: VideosComponent },
      { path: 'secretaria', component: SecretariaComponent },
    ]
  },
  { path: '**', redirectTo: '404', pathMatch: 'full' },
];


@NgModule({
  imports: [RouterModule.forRoot(routes, {
    useHash: true,
    enableTracing: false,
    preloadingStrategy: PreloadAllModules
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
